<template>
    <div class="wrapper wrapper-bg">
      <div class="w-menu">
        <!-- <div class="btn btn-l" v-if="navIndex!==0" @click="handleMenuChange('left')"></div> -->
        <!-- <div class="btn btn-r" v-if="navIndex!==1" @click="handleMenuChange('right')"></div> -->
        <div class="navbar">
          <ul class="ulbar" :style="{width:routeWrapperParam.length*200+'px',marginLeft:-navIndex*80+'px'}">
            <router-link
              v-for="(item, index) in this.routeWrapperParam"
              :key="index"
              class="item"
              :to="{path: item.link, query: {name: headerName}}"
              tag="li"
              active-class="active"
            >{{item.eachName}}
            </router-link>
          </ul>
        </div>
      </div>

      <transition>
        <router-view></router-view>
      </transition>
    </div>
</template>

<script>
  import {get_http} from "@/api/index";

  export default {
    name: "qt",
    components: {},
    data() {
      return {
        headerName: '',
        navIndex: 0,
        routeWrapperParam: [
          {link: "/rdmxfx/qt/jjf", eachName: "进京访"},
          {link: "/rdmxfx/qt/dsf", eachName: "到省访"}
        ]
      };
    },
    mounted() {
			this.headerName = this.$route.query.name
		},
    methods: {
      handleMenuChange(dir) {
        dir === 'left' ? this.navIndex-- : this.navIndex++;
      }
    }
  };
</script>

<style lang="stylus" scoped>
.wrapper-bg
  background url("../../../assets/wrapper-bg.png") no-repeat center
  background-size 100% 100%
.wrapper
  width 100%
  height 100%
  .w-menu
    position absolute
    top 5px
    left 40px
    z-index 100
    padding 0 38px
    .btn
      position absolute
      top 18px
      z-index 1000
      width 38px
      height 30px
      background url("../../../assets/icon-arrow.png") no-repeat
      cursor pointer
      &.btn-l
        left 16px
        transform rotate(180deg)
      &.btn-r
        right 16px
    .navbar
      width 420px
      height 65px
      overflow hidden
      z-index 2
      .ulbar
        display flex
        /*width 560px*/
        height 100%
        margin-left 0
        transition all .3s
        li.item
          flex 1
          cursor pointer
          font-size 24px
          line-height 65px
          color #cbfbff
          text-align center
          &.active
            font-weight 700
            color #fff
            text-shadow 0 0 5px #0cafc9
    .v-enter, v-leave
      opacity 0

    .v-enter-active
      transition opacity 1s

    .v-enter-to
      opacity 1
</style>
